import uuid from "react-uuid";
import nextId from "react-id-generator";
import styles from "./UserDrinksItem.module.css";

import { drinks, createNewDrink } from "../../services/drinkList";

const UserDrinksItem = ({ drinkInfo, count, setLastDrinksList }) => {
  const clickHandler = () => {
    setLastDrinksList((prevState) => {
      const newState = [...prevState, createNewDrink(drinkInfo.name, count)];

      localStorage.setItem("lastDrinks", JSON.stringify(newState));
      return newState;
    });
  };

  return (
    <div className={styles.drinkItem} onClick={clickHandler}>
      <img src={drinkInfo.img} alt="drink" className={styles.img} />
      <div className={styles.count}>{count} </div>
    </div>
  );
};

export default UserDrinksItem;
