export let data = [
  { name: "", uv: 0, waterRate: 5000 },
  { name: "", uv: 0, waterRate: 5000 },
  { name: "", uv: 0, waterRate: 5000 },
  { name: "", uv: 0, waterRate: 5000 },
  { name: "", uv: 0, waterRate: 5000 },
  { name: "", uv: 0, waterRate: 5000 },
  { name: "", uv: 0, waterRate: 5000 },
];

export const setChartData = (date, value) => {
  const dayNum = date.getDay();
  let day;
  switch (dayNum) {
    case 0:
      day = "";
      break;
    case 1:
      day = "";
      break;
    case 2:
      day = "";
      break;
    case 3:
      day = "";
      break;

    case 4:
      day = "";
      break;
    case 5:
      day = "";
      break;
    case 6:
      day = "";
      break;
    default:
      break;
  }

  data = data.map((item) => {
    if (item.name !== day) return item;

    item.uv = value;
    return item;
  });
};

export const setWaterRate = (count) => {
  data = data.map((item) => {
    item.waterRate = count;
    return item;
  });
};
