import { useState } from "react";
import { Button, Modal, Form } from "antd";

import StartForm from "../startForm/StartForm";

const Title = () => {
  return (
    <h2
      style={{
        fontSize: 20,
        textAlign: "center",
        marginBottom: 20,
        marginTop: 10,
      }}
    >
        
    </h2>
  );
};

const StartModal = ({ btnStyle, setUserInfo }) => {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  return (
    <>
      <Button className={btnStyle} type="primary" onClick={showModal}>
        !
      </Button>
      <Modal
        title={<Title />}
        open={isModalOpen}
        onOk={handleOk}
        onCancel={handleCancel}
        width={600}
        cancelText=""
        okText="!"
        footer={null}
      >
        <StartForm closeModal={handleOk} setUserInfo={setUserInfo} />
      </Modal>
    </>
  );
};
export default StartModal;
