import React from "react";
import classNames from "classnames";

import styles from "./AddDrinkForm.module.css";

const Drink = ({ drink, selected, setSelected }) => {
  return (
    <div
      className={classNames(styles.drinkItem, { [styles.selected]: selected })}
      onClick={() => setSelected(drink.name)}
    >
      <div className={styles.imgWrapper}>
        <img src={drink.img} alt="drink" className={styles.img} />
      </div>
      <div className={styles.drinkName}>{drink.displayName}</div>
    </div>
  );
};

export default Drink;
