import { Button, Form, Input, Select, InputNumber } from "antd";

import styles from "./StartForm.modal.css";

const { Option } = Select;

const layout = {
  labelCol: { span: 7 },
  wrapperCol: { span: 39 },
};

const StartForm = ({ closeModal, setUserInfo }) => {
  const [form] = Form.useForm();

  const onFinish = (values) => {
    setUserInfo(values);
    closeModal();
  };

  return (
    <Form
      {...layout}
      form={form}
      name="control-hooks"
      onFinish={onFinish}
      style={{
        maxWidth: 1000,
      }}
    >
      <Form.Item
        name="userName"
        label=" "
        rules={[
          {
            required: true,
            message: "   ",
          },
        ]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        name="gender"
        label=""
        rules={[
          {
            required: true,
            message: "   ",
          },
        ]}
      >
        <Select placeholder="  " allowClear>
          <Option value="male"></Option>
          <Option value="female"></Option>
        </Select>
      </Form.Item>

      <Form.Item
        name="activityLevel"
        label=" "
        rules={[
          {
            required: true,
            message: "    ",
          },
        ]}
      >
        <Select placeholder="  " allowClear>
          <Option value="low"> (1-2 a  )</Option>
          <Option value="medium"> (3-5   )</Option>
          <Option value="heavy"> (&gt;5   )</Option>
        </Select>
      </Form.Item>

      <Form.Item
        name="weight"
        label=" "
        rules={[
          {
            required: true,
            message: "   ",
          },
        ]}
      >
        <InputNumber min={40} max={200} />
      </Form.Item>

      <Form.Item style={{ display: "flex", justifyContent: "flex-end" }}>
        <Button
          type="default"
          htmlType="button"
          onClick={closeModal}
          style={{ marginRight: 10 }}
        >
          
        </Button>
        <Button type="primary" htmlType="submit">
          
        </Button>
      </Form.Item>
    </Form>
  );
};
export default StartForm;
