import LastDrinksItem from "../lastDrinksItem/LastDrinksItem";
import uuid from "react-uuid";

import styles from "./LastDrinks.module.css";

const LastDrinks = ({ lastDrinksList, setLastDrinksList }) => {
  return (
    <div className={styles.drinksWrapper}>
      {lastDrinksList.length === 0 && <p>   </p>}
      {lastDrinksList.map(({ drinkInfo, count, id, date }) => (
        <LastDrinksItem
          drink={drinkInfo}
          count={count}
          id={id}
          date={date}
          key={id}
          setLastDrinksList={setLastDrinksList}
        />
      ))}
    </div>
  );
};

export default LastDrinks;
