import { Button, Slider, InputNumber } from "antd";
import Drink from "./Drink";
import uuid from "react-uuid";
import nextId from "react-id-generator";

import { drinks, createNewDrink } from "../../services/drinkList";

import styles from "./AddDrinkForm.module.css";

import { useState } from "react";

const AddDrinkForm = ({ closeModal, setDrinksList, setLastDrinksList }) => {
  const [count, setCount] = useState(300);
  const [selected, setSelected] = useState(null);

  const onFinish = () => {
    if (!selected) return;
    if (count < 100 || count > 1000) return;

    //   
    setDrinksList((prevState) => {
      if (prevState.length === 6) {
        prevState.shift();
      }
      const newState = [...prevState, { name: selected, count }];

      localStorage.setItem("userDrinks", JSON.stringify(newState));
      return newState;
    });

    //     
    setLastDrinksList((prevState) => {
      const newState = [...prevState, createNewDrink(selected, count)];

      localStorage.setItem("lastDrinks", JSON.stringify(newState));
      return newState;
    });

    closeModal();
  };

  const handlechange = (value) => {
    setCount(value);
  };

  return (
    <div className={styles.formWrapper}>
      <InputNumber
        size="large"
        min={1}
        max={100000}
        step={10}
        value={count}
        onChange={handlechange}
        style={{
          display: "block",
          width: "100%",
          margin: "0 auto",
          textAlign: "center",
        }}
        placeholder={"   "}
      />
      <Slider
        min={100}
        max={1000}
        step={10}
        value={count}
        onChange={handlechange}
      />
      <div className={styles.drinks}>
        {Object.values(drinks).map((drink) => (
          <Drink
            key={uuid()}
            drink={drink}
            selected={drink.name === selected}
            setSelected={setSelected}
          />
        ))}
      </div>
      <Button
        style={{ display: "block", margin: "20px auto 10px auto" }}
        onClick={onFinish}
      >
        
      </Button>
    </div>
  );
};
export default AddDrinkForm;
