import styles from "./Bottle.module.css";
import bottleImg from "../../img/bottle3.svg";
import Chart from "../chart/Chart";

const Bottle = ({ totalCount, waterRate }) => {
  const progress = (totalCount / waterRate) * 100 * 0.86;
  return (
    <div className={styles.bottleContainer}>
      <Chart />
      <img className={styles.bottleImg} src={bottleImg} alt="bottle" />
      <div className={styles.water}>
        <div className={styles.wave}></div>
        <div
          className={styles.deepWater}
          style={{ height: `${progress}%` }}
        ></div>
      </div>
    </div>
  );
};

export default Bottle;
