import { useState } from "react";
import { Progress } from "antd";

import Waves from "../components/waves/Waves";
import Bottle from "../components/bottle/Bottle";
import LastDrinks from "../components/lastDrinks/LastDrinks";
import UserDrinks from "../components/userDrinks/UserDrinks";
import ProgressBar from "../components/progressBar/ProgressBar";
import { setChartData } from "../services/chartData";

const MainPage = ({ waterRate }) => {
  const getLastDrinksList = () => {
    return JSON.parse(localStorage.getItem("lastDrinks")) || [];
  };
  const [lastDrinksList, setLastDrinksList] = useState(getLastDrinksList());

  const totalCount = () => {
    const count = lastDrinksList.reduce(
      (prev, curr) => (prev += curr.count),
      0
    );
    setChartData(new Date(), count);
    return count;
  };

  //
  // set day
  let currentDay = +localStorage.getItem("currentDay");
  if (!localStorage.getItem("currentDay")) {
    currentDay = new Date().getDay();
    localStorage.setItem("currentDay", currentDay - 1);
  }
  if (currentDay !== new Date().getDay()) {
    currentDay = new Date().getDay();
    localStorage.setItem("currentDay", currentDay);
    localStorage.removeItem("lastDrinks");
    // set weekly rate
    const weeklyRate = localStorage.getItem("weeklyRate") || {};

    const prevDay = currentDay === 0 ? 6 : currentDay - 1;
    localStorage.setItem(
      "weeklyRate",
      JSON.stringify({
        ...weeklyRate,
        [prevDay]: totalCount(),
      })
    );
    setLastDrinksList([]);
  }

  return (
    <>
      <Waves />
      <div className="container">
        <ProgressBar waterRate={waterRate} totalCount={totalCount()} />
        <div className="drinksContainer">
          <Bottle totalCount={totalCount()} waterRate={waterRate} />
          <LastDrinks
            lastDrinksList={lastDrinksList}
            setLastDrinksList={setLastDrinksList}
          />
        </div>
        <UserDrinks setLastDrinksList={setLastDrinksList} />
      </div>
    </>
  );
};

export default MainPage;
