import React, { PureComponent, useState } from "react";
import { Button, Modal } from "antd";
import styles from "./Chart.module.css";

import stats from "../../img/stats.png";

import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
  CartesianGrid,
} from "recharts";

import { data } from "../../services/chartData";

const ChartInner = () => {
  return (
    <BarChart width={350} height={400} data={data}>
      <XAxis dataKey="name" stroke="#8884d8" />
      <YAxis dataKey="waterRate" />
      <Tooltip wrapperStyle={{ width: 100, backgroundColor: "#ccc" }} />

      <CartesianGrid stroke="#ccc" strokeDasharray="5 5" />
      <Bar dataKey="uv" fill="#8884d8" barSize={20} />
    </BarChart>
  );
};

const Chart = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  return (
    <>
      <Button type="primary" onClick={showModal} className={styles.btn}>
        <img src={stats} alt="statistics" className={styles.stats} />
      </Button>
      <Modal
        title="  "
        open={isModalOpen}
        onOk={handleOk}
        onCancel={handleCancel}
        width={370}
        centered={true}
        bodyStyle={{ position: "relative" }}
      >
        <div className={styles.chart}>
          <ChartInner />
        </div>
      </Modal>
    </>
  );
};

export default Chart;
