import { useState } from "react";
import { Button, Modal } from "antd";

import AddDrinkForm from "../addDrinkForm/AddDrinkForm";
import plus from "../../img/plus.svg";

const Title = () => {
  return (
    <h2
      style={{
        fontSize: 20,
        textAlign: "center",
        marginBottom: 20,
        marginTop: 10,
      }}
    >
       
    </h2>
  );
};

const AddDrinkModal = ({ setDrinksList, setLastDrinksList }) => {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  return (
    <>
      <Button
        type="primary"
        onClick={showModal}
        style={{
          padding: 0,
          borderRadius: "50%",
          width: "40px",
          height: "40px",
          marginRight: "10px",
        }}
      >
        <img src={plus} alt="plus" style={{ width: "40px", height: "40px" }} />
      </Button>
      <Modal
        title={<Title />}
        open={isModalOpen}
        onOk={handleOk}
        onCancel={handleCancel}
        width={600}
        cancelText=""
        okText="!"
        footer={null}
        bodyStyle={{ backgroundColor: "#81cdfe", borderRadius: 10 }}
      >
        <AddDrinkForm
          closeModal={handleOk}
          setDrinksList={setDrinksList}
          setLastDrinksList={setLastDrinksList}
        />
      </Modal>
    </>
  );
};
export default AddDrinkModal;
