import { useState } from "react";

import uuid from "react-uuid";

import styles from "./UserDrinks.module.css";
import UserDrinksItem from "../userDrinksItem/UserDrinksItem";
import AddDrinkModal from "../addDrinkModal/AddDrinkModal";

import { drinks, createNewDrink } from "../../services/drinkList";

const UserDrinks = ({ setLastDrinksList }) => {
  const getDrinksList = () => {
    return JSON.parse(localStorage.getItem("userDrinks")) || [];
  };

  const [drinksList, setDrinksList] = useState(getDrinksList());
  return (
    <div className={styles.drinksWrapper}>
      <div className={styles.drinksList}>
        {drinksList.map(({ name, count }) => {
          const drinkInfo = drinks[name];
          return (
            <UserDrinksItem
              drinkInfo={drinkInfo}
              count={count}
              key={uuid()}
              setLastDrinksList={setLastDrinksList}
            />
          );
        })}
      </div>
      <AddDrinkModal
        setDrinksList={setDrinksList}
        setLastDrinksList={setLastDrinksList}
      />
    </div>
  );
};

export default UserDrinks;
