import StartModal from "../startModal/StartModal";

import styles from "./Initial.module.css";
import moon from "../../img/moon.png";

const Initial = ({ setUserInfo }) => {
  return (
    <section className={styles.initialize}>
      <img className={styles.moon} src={moon} alt="moon" />
      <h1 className={styles.title}>Water Tracker</h1>
      <StartModal btnStyle={styles.btn} setUserInfo={setUserInfo} />
      <div className={styles.waves}>
        <div className={styles.wave} id={styles.wave1}></div>
        <div className={styles.wave} id={styles.wave2}></div>
        <div className={styles.wave} id={styles.wave3}></div>
        <div className={styles.wave} id={styles.wave4}></div>
      </div>
    </section>
  );
};

export default Initial;
