import { Popconfirm } from "antd";
import { QuestionCircleOutlined } from "@ant-design/icons";
import styles from "./LastDrinksItem.module.css";

const lastDrinksItem = ({ drink, count, id, date, setLastDrinksList }) => {
  const confirm = (e) => {
    setLastDrinksList((prevState) => {
      const newDrinks = prevState.filter((drinkItem) => drinkItem.id !== id);
      return newDrinks;
    });
  };

  return (
    <Popconfirm
      title=" ?"
      icon={<QuestionCircleOutlined style={{ color: "red", width: 10 }} />}
      onConfirm={confirm}
      okText=""
      cancelText=""
      style={{ maxWidth: 100 }}
    >
      <div className={styles.drinkItem}>
        <img src={drink.img} alt="drink" className={styles.drinkImg} />
        <div className={styles.drinkTime}>{date}</div>
        <div className={styles.drinkName}>{drink.displayName}</div>
        <div className={styles.drinkCount}>{count} </div>
      </div>
    </Popconfirm>
  );
};

export default lastDrinksItem;
