import { ConfigProvider, theme } from "antd";
import { useEffect, useState } from "react";

import InitialPage from "./pages/InitialPage";
import MainPage from "./pages/MainPage";
import { setWaterRate } from "./services/chartData";

const App = () => {
  const [userInfo, setUserInfo] = useState(null);
  const waterRate = localStorage.getItem("waterRate");
  setWaterRate(waterRate);

  if (userInfo && !waterRate) {
    const { activityLevel, gender, userName, weight } = userInfo;

    let activityTime = 0;

    switch (activityLevel) {
      case "low":
        activityTime = 1.5;
        break;
      case "medium":
        activityTime = 3;
        break;
      case "heavy":
        activityTime = 5;
        break;
      default:
        break;
    }

    const waterRate =
      (weight * 0.03 + activityTime * (gender === "male" ? 0.6 : 0.4)) * 1000;

    localStorage.setItem("waterRate", waterRate);
    localStorage.setItem("userName", userName);
  }

  //
  const main = waterRate && <MainPage waterRate={waterRate} />;
  const init = !waterRate && (
    <ConfigProvider
      theme={{
        algorithm: theme.darkAlgorithm,
      }}
    >
      <InitialPage setUserInfo={setUserInfo} />
    </ConfigProvider>
  );

  return (
    <>
      {main}
      {init}
    </>
  );
};

export default App;
